.benefits {
  width: 100%;
  margin: 0 auto;
}

.benefits__wrapper{
  width: 90%;
  margin: 0 auto;
  padding: 20px;
}

.benefits__list {
  margin: 0 auto;
  margin-top: 50px;
  padding: 0;
}

.benefits__item {
  list-style: none;
  box-sizing: border-box;
  display: block;
  min-height: 80px;
  margin-top: 5px;
}

@media (min-width: $tablet-width) {

  .benefits__list {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }

  .benefits__item {
    width: 40%;
  }

}

@media (min-width: $desktop-width) {

  .benefits__wrapper {
  width: 1200px;
  }

  .benefits__list {
    margin-bottom: 30px;
  }

  .benefits__item {
    width: 200px;
  }


}
