.options {
  width: 100%;
  margin: 0 auto;
}

.options__wrapper{
  margin: 0 auto;
  width: 90%
}

.options__list {
  margin: 0 auto;
  padding: 0;
  margin: 40px auto;
}

.options__item {
  list-style: none;
  box-sizing: border-box;
  display: block;
  min-height: 265px;
  background-color: $main-color;
  margin: 15px auto;
  padding: 10px 20px;
}

.options__title {
  font-size: 22px;
  text-transform: uppercase;
  padding: 20px;
  padding-left: 80px;
  position: relative;
}

.options__title::before {
  content: " ";
  position: absolute;
  left: 5px;
  bottom: 5px;
  background-repeat: no-repeat;
  background-color: #68b738;
  border-radius: 50%;
  display: block;
  height: 60px;
  width: 60px;
}

.options__title--gain::after {
    content: " ";
    position: absolute;
    left: 1px;
    top: 1px;
    background-repeat: no-repeat;
    display: block;
    height: 75px;
    width: 75px;
    background-image: url(../img/catpro.svg);
    background-position: 50% 20%;
}

.options__title--lose::after {
  content: " ";
  position: absolute;
  left: 1px;
  bottom: 1px;
  background-repeat: no-repeat;
  display: block;
  height: 75px;
  width: 75px;
  background-image: url(../img/catslim.svg);
  background-position: 50% 50%;
}

.options__arrow {
  display: block;
  text-transform: uppercase;
  border-top: 1px solid darken($main-color, 30%);
  position: relative;
  padding-top: 15px;
  padding-bottom: 15px;
  margin: 0;
  font-weight: bold;
  font-size: 16px;

}

.options__arrow::after {
    content: " ";
    font-size: 16px;
    text-align: center;
    position: absolute;
    left: 140px;
    bottom: 15px;
    background-image: url("../img/arrow.png");
    background-repeat: no-repeat;
    display: block;
    height: 15px;
    width: 30px;
  }


