.example {
  width: 100%;
  background-color: $main-color;

}

.example__wrapper {
  width: 90%;
  margin: 0 auto;
  box-sizing: border-box;
  padding-top: 10%;
}

.example__extra-title {
  display: none;
}

@media (min-width: $tablet-width) {
  .example {

  background-color: white;

  }

  .example__wrapper {
    border-top: 0.5px solid #dcdcdc;
    margin: 0 auto;
  }

  .example__cat-wrapper {
    background-color: $main-color;
    margin-top: 10%;
    text-align: center;
  }

  .example__cat {
    margin-top: -10%;
  }

}

@media (min-width: $desktop-width) {

  .example {
    margin-top: 3%;
  }

  .example__title {
    display: none;

  }

  .example__extra-title {
    display: block;
    width: 1200px;
    margin: 0 auto;
  }

  .example__wrapper {
    margin-top: 40px;
    padding-left: 15%;
    width: 100%;
    background-color: $main-color;
    display: flex;
    justify-content: space-around;
    border: none;
  }



  .example__extra-wrapper,
  .example__cat-wrapper {
    width: 50%;
    margin: 0;
    padding: 3%;
  }

  .example__cat {
    margin-top: -25%;
  }

}
